.contact {
    padding-left: 10px;
    margin-top: 10px;
}

.profileData {
    margin-bottom: 20px;
}

.profileDataItem {
    margin-bottom: 10px;
}

.editButton,
.saveButton {
    background-color: transparent;
    border: 1px solid #b1b3b5;
    color: #fff;
    padding: 5px 15px;
    font-size: 18px;
    cursor: pointer;
    margin-top: 15px;
    font-family: 'IBMPlexSerif', serif;
}

.editButton:hover,
.saveButton:hover {
    color: #af8a31;
    border-color: #af8a31;
    transition-duration: 500ms;
}

.fieldLabel {
    margin-bottom: 15px;
}