.contentHeader {
    max-width: 1000px;
    max-height: 200px;
    overflow: hidden;
}

.descriptionWrapper {
    padding: 10px;
}

.mainPhoto {
    max-width: 200px;
}

.contact {
    padding-left: 10px;
}

.userAvatarSection {
    margin-bottom: 30px;
}

.mainPhotoWrapper {
    margin-bottom: 20px;
}

.mainPhoto {
    border: 1px solid #b1b3b5;
}

.mainPhotoUploader {
    opacity: 0;
    width: 1px;
    height: 1px;
}

.mainPhotoUploaderLabel {
    background-color: transparent;
    border: 1px solid #b1b3b5;
    color: #fff;
    padding: 5px 10px;
    font-size: 18px;
    cursor: pointer;
}

.mainPhotoUploaderLabel:hover {
    color: #af8a31;
    border-color: #af8a31;
    transition-duration: 500ms;
}

.userAvatarSection {
    margin-right: 50px;
}

.userProfileHeaderWrapper {
    display: flex;
}

.myStatusHeading {
    display: inline-block;
    margin-bottom: 10px;
}